import Vue from 'vue'
import Vuex from 'vuex'
import * as types from '../utils/type';
import contextBasic from './modules/IndexBasic';


Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production'
export default new Vuex.Store({
    state:{
        loginState:false,
        authInfo:null
    },
    actions:{
        async getAuthorizationData(commit){
            
        }
    },
    mutations:{
        //获取登录信息
        [types.GET_AUTHORIZATION_DATA](state,res){
            state.authInfo = res;
        }
    },
    modules:{
        contextBasic
    },
    strict:debug,
    plugins:[]
})


