<template>
    <van-tabbar v-model="initActive" :change="onChange">
        <van-tabbar-item v-for="(icon,key) in icons" :key="key" :info="icon.info" :to="icon.to">
            <span>{{icon.text}}</span>
            <svg 
                class="icon-navbar" 
                aria-hidden="true"
                slot="icon"
            >
                <use :xlink:href="active == key ? icon.activeSvg : icon.normalSvg"></use>
            </svg>
            <!-- <img
           
            v-lazy="active == key ? icon.active : icon.normal"
            > -->
        </van-tabbar-item>
    </van-tabbar>
</template>
<script>
export default {
    props:{
        active:{
            type:Number,
            default:0
        }
    },
    data(){
        return {
            //通过prop传入的参数 最好在data或者计算属性中赋值
            initActive:this.active,
            loadingIcon:'',
            icons:[
                {
                    normal:'static/img/lazybar/home@2x.png',
                    active:'static/img/lazybar/home_sel@2x.png',
                    normalSvg:'#icon-shouyeweixuanzhong',
                    activeSvg:'#icon-shouyexuanzhong',
                    to:'index',
                    text:'首页',
                    info:''
                },
                {
                    normal:'static/img/lazybar/group@2x.png',
                    active:'static/img/lazybar/group_sel@2x.png',
                    normalSvg:'#icon-fenleiweixuanzhong',
                    activeSvg:'#icon-fenleixuanzhong1',
                    to:'group',
                    text:'商品分组',
                    info:''
                },
                {
                    normal:'static/img/lazybar/cart@2x.png',
                    active:'static/img/lazybar/cart_sel@2x.png',
                    normalSvg:'#icon-gouwucheweixuanzhong',
                    activeSvg:'#icon-gouwuchexuanzhong',
                    to:'cart',
                    text:'购物车',
                    info:''
                },
                {
                    normal:'static/img/lazybar/my@2x.png',
                    active:'static/img/lazybar/my_sel@2x.png',
                    normalSvg:'#icon-wodeweixuanzhong',
                    activeSvg:'#icon-wodexuanzhong',
                    to:'user',
                    text:'个人中心',
                    info:''
                }
            ]
        }
    },
    methods:{
        onChange:(active)=>{
            console.log('当前变化的',active);
        }
    },
    watch:{
        active:function(newVal,oldVal){
            this.initActive = newVal;
        }
    }
}
</script>



