<template>
    <div class="loading-layout" @touchmove.prevent @scroll.prevent v-show="visible" :class="isOpacity ? 'showBg': '' ">
        <div class="loading-content">
            <span class="van-loading__spinner van-loading__spinner--circular">
                <svg viewBox="25 25 50 50" class="van-loading__circular">
                    <circle cx="50" cy="50" r="20" fill="none"></circle>
                </svg>
            </span>
            <!-- <div class="van-loading-info"></div> -->
        </div>
    </div>
</template>
<script>
export default {
    name:'loading',
    data(){
        return{
            visible:false,
            isOpacity:false
        }
    },
    methods:{
        close(){
            this.visible = false;
            //关闭的时候 恢复默认值
            this.isOpacity=false;
        },
        open(options){
            if(options && options.opacity){
                console.log(options);
                this.isOpacity = options.opacity
            }
            this.visible = true;
        }
    }
}
</script>
<style lang="stylus" scoped>
    @-webkit-keyframes van-rotate {
        from {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        to {
            -webkit-transform: rotate(360deg);
                    transform: rotate(360deg);
        }
    }
    @keyframes van-rotate {
        from {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        to {
            -webkit-transform: rotate(360deg);
                    transform: rotate(360deg);
        }
    }
    .showBg
        background rgba(0,0,0,.3)
    .loading-layout
        position fixed
        top 0
        left 0
        bottom 0
        right 0
        z-index 9
        display flex
        justify-content center
        align-content center
        .loading-content
            padding: 20px;
            border-radius: 6px;
            background-color: #fff;
            width: 60px;
            height: 60px;
            z-index: 0;
            font-size: 0;
            line-height: 0;
            position: absolute;
            vertical-align: middle;
            top:50%;
            left:50%;
            transform :translate(-50%,-50%);
            .van-loading__spinner--circular
                -webkit-animation-duration: 2s;
                animation-duration: 2s;
            .van-loading__spinner
                z-index: -1;
                width: 100%;
                height: 100%;
                position: relative;
                display: inline-block;
                box-sizing: border-box;
                -webkit-animation: van-rotate 0.8s linear infinite;
                animation: van-rotate 0.8s linear infinite;
                .van-loading__circular
                    width: 100%;
                    height: 100%;

</style>



