import LoadingMaskComponents from './src/loading.vue';
const LoadingMask = {};
let tpl;
let instance;

LoadingMask.install = function(Vue,PluginOptions){
    const LoadingMaskConstructor = Vue.extend(LoadingMaskComponents);
    if(!instance){
        instance = new LoadingMaskConstructor();
    }
    if(instance.visible) return;
    tpl = instance.$mount().$el;
    document.body.appendChild(tpl);
    Vue.mixin({
        methods:{
            $LoadingMaskOpen:(options = {}) => {
                Vue.nextTick(()=>{
                    instance.open(options);
                })
            },
            $LoadingMaskClose:() => {
                instance.close();
            }
        }
    })
    
}

LoadingMask.open = (options) => {
    instance.open(options);
}

LoadingMask.close = () => {
    instance.close();
}

export default LoadingMask;


